<script setup lang="ts">
import { ref, watch, onMounted } from 'vue';
import * as echarts from 'echarts';

interface SeriesItem {
  name: string;
  type: string;
  axis?: string;
}

interface AxisConfig {
  key: string;
  type: 'time' | 'category' | 'value';
  title: string;
  timeGrain?: string;
}

interface AxisChartConfig {
  data: any[];
  title: string;
  subtitle?: string;
  xAxis: AxisConfig;
  yAxis: AxisConfig;
  y2Axis?: AxisConfig;
  stacked?: boolean;
  swapXY?: boolean;
  series: SeriesItem[];
  // additional config fields here
}

const props = defineProps<{ config: AxisChartConfig }>();

const chartRef = ref<HTMLDivElement | null>(null);
let chartInstance: echarts.ECharts | null = null;

const initChart = () => {
  if (!chartRef.value) return;
  if (chartInstance) {
    chartInstance.dispose();
  }
  chartInstance = echarts.init(chartRef.value);

  const option = buildChartOption(props.config);
  chartInstance.setOption(option);
};

const buildChartOption = (config: AxisChartConfig) => {
  const xAxisData = config.data.map(d => d[config.xAxis.key]);
  const series = config.series.map(s => ({
    name: s.name,
    type: s.type,
    stack: config.stacked ? 'total' : undefined,
    data: config.data.map(d => d[s.name]),
    yAxisIndex: s.axis === 'y2' ? 1 : 0,
  }));

  const option = {
    title: {
      text: config.title,
      subtext: config.subtitle,
    },
    tooltip: {
      trigger: 'axis',
    },
    legend: {
      data: config.series.map(s => s.name),
    },
    xAxis: {
      type: config.xAxis.type,
      data: config.xAxis.type === 'category' ? xAxisData : undefined,
      name: config.xAxis.title,
    },
    yAxis: [
      {
        type: 'value',
        name: config.yAxis.title,
        min: 0,
      },
      config.y2Axis
        ? {
            type: 'value',
            name: config.y2Axis.title,
            min: 0,
          }
        : null,
    ].filter(Boolean),
    series,
  };
  return option;
};

watch(() => props.config, () => {
  initChart();
}, { deep: true });

onMounted(() => {
  initChart();
  window.addEventListener('resize', () => chartInstance?.resize());
});
</script>

<template>
  <div ref="chartRef" style="width: 100%; height: 400px;"></div>
</template>
